import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Distribution(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/distribution.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[-0.57, 3.76, -1.65]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere170.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere170_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere170_2.geometry}
          material={materials.outline}
        />
      </group>
    </group>
  );
}
export default Distribution;
useGLTF.preload("/glossarySelected/distribution.glb");
